import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp } from "../../../../../components/helpers/function";
class ExportTransaksiPenjualanExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    let harga_jual = 0;
    let harga_atribut = 0;
    let harga_gram = 0;
    let harga_total = 0;
    let ongkos = 0;
    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
        harga_jual = harga_jual + item.harga_jual;
        ongkos = ongkos + item.ongkos;
        harga_total = harga_total + item.harga_total;
        harga_gram = harga_gram + item.harga_gram;
        harga_atribut = harga_atribut + item.harga_atribut;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Transaksi Penjualan Bayar"
          sheet="Laporan Transaksi Penjualan Bayar"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="10"> Periode </th>
            </tr>
            <tr>
              <th colSpan="10">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="10"></th>
            </tr>
            <tr>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    NO FAKTUR
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    NAMA BARANG
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    BERAT
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    ONGKOS
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    HARGA JUAL
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    NAMA ATRIBUT
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    BERAT ATRIBUT
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    HARGA ATRIBUT
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    HARGA GRAM
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
                    HARGA TOTAL
                  </td>
                </tr>
          </thead>
          {this.props.data.map((item, index) => (
            <>
              <tbody>
                {item._id.pembayaran.map((header_detail, index) => (
                  <>
                    {/* <tr>
                      <td style={{ textAlign: "left" }}>No Faktur Group</td>
                      <td colSpan="9" style={{ border: "1px solid #000" }}> {header_detail.no_faktur_group}</td>
                    </tr> */}
                    <tr>
                      <td colSpan="10" style={{ border: "1px solid #000" }}>
                        {header_detail.jenis}{" "}
                        {parseInt(header_detail.jumlah_rp).toLocaleString(
                          "ID-id"
                        )}
                      </td>
                    </tr>
                  </>
                ))}
               
                {item.detail.map((row, index) => (
                  <tr>
                    <td style={{ border: "1px solid #000" }}>
                      {row.no_faktur_jual}
                    </td>
                    <td
                      style={{ border: "1px solid #000", whiteSpace: "nowrap" }}
                    >
                      {row.nama_barang}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.berat}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.ongkos}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.harga_jual}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.nama_atribut}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.berat_atribut}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.harga_atribut}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.harga_gram}
                    </td>
                    <td
                      style={{ border: "1px solid #000" }}
                    >
                      {row.harga_total}
                    </td>
                  </tr>
                ))}
              </tbody>
              <tfoot>
                <tr>
                  <td colSpan="2" style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000", textAlign: "center" }}>
                    Total Barang : {item.detail.length}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.berat)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.ongkos)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.harga_jual)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.berat_atribut)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.harga_atribut)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.harga_gram)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#008846", color: "#FFFFFF", border: "1px solid #000" }}>
                    {" "}
                    {item.detail
                      .map((list) => list.harga_total)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                </tr>
              </tfoot>
            </>
          ))}
          <tr>
            <td colSpan="2" style={{ border: "1px solid #000",backgroundColor: "#008846", color: "#FFFFFF",textAlign:"center" }}>
             Grand Total Barang : {total_barang}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {berat}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {ongkos}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {harga_jual}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}> </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {berat_atribut}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {harga_atribut}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {harga_gram}
            </td>
            <td style={{ backgroundColor: "#008846", color: "#FFFFFF",border: "1px solid #000" }}>
              {harga_total}
            </td>
          </tr>
        </table>
      </>
    );
  }
}
export default ExportTransaksiPenjualanExel;
